___CSS_LOADER_EXPORT___.push([module.id, `.acajatexto{
    display: flex;
    flex-direction: column;
}

.acajatexto-titulo{
    font-size: 15px;
    font-weight: 500;
    margin-bottom: 0.5rem;
}

.acajatexto-no-visible{
    display: none;
}

.acajatexto-contenedor-input{
    display: flex;
    flex-direction: row;
    align-items: center;
    border: 1px solid #ccc;
    border-radius: 10px;
    transition: all 0.3s ease;
}

.acajatexto-contenedor-input:focus-within{
    border: 1px solid var(--primario-acajatexto-borde);
    box-shadow: 0 0 0 0.2px var(--primario-acajatexto-borde);
    transition: all 0.3s ease;
}

.acajatexto-icono-izquierda{
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0.5rem;
}

.acajatexto-input{
    flex: 1;
    border: none;
    padding: 0.5rem;
    font-size: 15px;
    font-weight: 500;
    outline: none;
}

.acajatexto-icono-derecha{
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0.5rem;
}`, "",{"version":3,"sources":["webpack://./src/acomponentes/ACajaTexto/acajatexto.css"],"names":[],"mappings":"AAAA;IACI,aAAa;IACb,sBAAsB;AAC1B;;AAEA;IACI,eAAe;IACf,gBAAgB;IAChB,qBAAqB;AACzB;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,mBAAmB;IACnB,sBAAsB;IACtB,mBAAmB;IACnB,yBAAyB;AAC7B;;AAEA;IACI,kDAAkD;IAClD,wDAAwD;IACxD,yBAAyB;AAC7B;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,uBAAuB;IACvB,eAAe;AACnB;;AAEA;IACI,OAAO;IACP,YAAY;IACZ,eAAe;IACf,eAAe;IACf,gBAAgB;IAChB,aAAa;AACjB;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,uBAAuB;IACvB,eAAe;AACnB","sourcesContent":[".acajatexto{\r\n    display: flex;\r\n    flex-direction: column;\r\n}\r\n\r\n.acajatexto-titulo{\r\n    font-size: 15px;\r\n    font-weight: 500;\r\n    margin-bottom: 0.5rem;\r\n}\r\n\r\n.acajatexto-no-visible{\r\n    display: none;\r\n}\r\n\r\n.acajatexto-contenedor-input{\r\n    display: flex;\r\n    flex-direction: row;\r\n    align-items: center;\r\n    border: 1px solid #ccc;\r\n    border-radius: 10px;\r\n    transition: all 0.3s ease;\r\n}\r\n\r\n.acajatexto-contenedor-input:focus-within{\r\n    border: 1px solid var(--primario-acajatexto-borde);\r\n    box-shadow: 0 0 0 0.2px var(--primario-acajatexto-borde);\r\n    transition: all 0.3s ease;\r\n}\r\n\r\n.acajatexto-icono-izquierda{\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: center;\r\n    padding: 0.5rem;\r\n}\r\n\r\n.acajatexto-input{\r\n    flex: 1;\r\n    border: none;\r\n    padding: 0.5rem;\r\n    font-size: 15px;\r\n    font-weight: 500;\r\n    outline: none;\r\n}\r\n\r\n.acajatexto-icono-derecha{\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: center;\r\n    padding: 0.5rem;\r\n}"],"sourceRoot":""}]);
